
export function codeColor(node) {
    
	switch (node.code[2]) {
      case "1":
        return "#5db061";
      case "2":
        return "#c18541";
      case "3":
        return "#c44444";
      case "4":
        return "#4a93c8";
      case "5":
        return "#7f5fd0";
      default:
        return "#888";
    }
};

export function semesterColor(node) {
    switch (node.semester) {
      case "høst":
        return "#aa8c55";
      case "vår":
        return "#55a";
	  default:
	    return "#aaa";
    }
};

export function pointsColor(node) {
   
	let oldMax = 30;
  	let newMax = 100;
  	let perc = (node.points * newMax) / oldMax;
	
	var r,g,b = 0;
	
	if (perc < 50) {
		r = 255;
		g = Math.round(5.1 * perc);
	}
	
	else {
		g = 255;
		r = Math.round(510 - 5.1 * perc);
	}
	
	var h = r * 0x10000 + g * 0x100 + b * 0x1;
	return "#" + ("000000" + h.toString(16)).slice(-6);
};
