import React, { Component } from "react";
import MyNavbar from "./components/Navbar";

import { ForceGraph } from "./components/graphing/ForceGraph";
import data from "./data/sample2.json";
import Subjects from "./components/Subjects";
import SemesterPlan from "./components/Semesterplan";
import {codeColor, semesterColor, pointsColor } from "./components/NodeColor";

class App extends Component {
  state = {
    selected: [],
    topsorted: [],
	sems: [],
    sortBy: "",
	pos: [],
	// thisfunc : null,
	funcColor: codeColor
  };

  //
  addSubs = (e) => {
    const node = e.currentTarget.getAttribute("data-item");
    const temp = data.nodes.filter((a) => a.code === node)[0];

    const { selected, thisfunc } = this.state;

    if (!selected.includes(temp)) {
      const result = this.recursiveAdd([temp]);
      const result2 = result.filter((n) => !selected.includes(n));
      const result3 = [...new Set(result2)];

      this.setState({
        selected: [...this.state.selected, ...result3],
      });
    } else {
      this.setState({
        selected: selected.filter((sub) => sub !== temp),
      });
    }
    thisfunc();
  };
  
  setColorMode = (e) => {
	const mode = e.currentTarget.getAttribute("data-item");
	let temp;
	
    const { thisfunc } = this.state;
	
	console.log(mode)
	
	switch (mode) {
		case "code":
			temp = codeColor
			break;
		case "semester":
			temp = semesterColor;
			break;
		case "points":
			temp = pointsColor;
			break;
	}
	
	this.setState({
        funcColor: temp,
    });
	
	thisfunc()
  }

  recursiveAdd = (nodeList) => {
    let result = [...nodeList];

    for (const node of nodeList) {
      if (node.msubs != null) {
        let rec = data.nodes.filter((n) => node.msubs.includes(n.id));
        result = [...result, ...this.recursiveAdd(rec)];
      }

      if (node.rsubs != null) {
        let rec = data.nodes.filter((n) => node.rsubs.includes(n.id));
        result = [...result, ...this.recursiveAdd(rec)];
      }
    }

    return result;
  };

  clearSubs = () => {
    const { thisfunc } = this.state;

    this.setState({
      selected: [],
    });

    thisfunc();
  };

  //
  setFunc = (e) => {
    this.setState({
      thisfunc: e,
    });
  };

  //
  tsFunc = (e) => {
    this.setState({
      topsorted: e,
    });
  };
  
  tssFunc = (e) => {
    this.setState({
      sems: e,
    });
  };

  chooseSort = (e) => {
    this.setState({
      sortBy: e.currentTarget.getAttribute("data-item"),
    });
  };

  //
  nodeHoverTooltip = (node) => {
    return node.title;
  };
  
  funcPos = (arr) => {	
	this.setState({
      pos : arr,
    });
  }

  render() {
    const { sortBy, topsorted, selected, thisfunc, sems, funcColor, pos } = this.state;

    return (
      <div className="App">
        <MyNavbar></MyNavbar>
        <div className="app-container">
          <section className="left">
			<div>
	            <Subjects
	              nodesData={data.nodes}
	              selected={selected}
	              sortBy={sortBy}
	              funcColor={funcColor}
	              funcSelect={this.addSubs}
	              funcSort={this.chooseSort}
	              funcClear={this.clearSubs}
	            />
				<div style={{display:"inline-block"}}>
					<button data-item={"code"} onClick={this.setColorMode}>code</button>
					<button data-item={"semester"} onClick={this.setColorMode}>semester</button>
					<button data-item={"points"} onClick={this.setColorMode}>points</button>
				</div>
			</div>
          </section>

          <section className="middle">
            <SemesterPlan
              nodesData={selected}
              linksData={data.links}
              funcColor={funcColor}
              result={topsorted}
              tsFunc={this.tsFunc}
              tssFunc={this.tssFunc}
              sems={sems}
            />
          </section>

          <section className="right">
            <ForceGraph
              linksData={data.links}
              nodesData={selected}
              funcColor={funcColor}
              nodeHoverTooltip={this.nodeHoverTooltip}
              mytemp={this.setFunc}
			  funcPos={this.funcPos}
			  pos={pos}
            />
          </section>
        </div>
      </div>
    );
  }
}

export default App;
